#root {
  max-width: 80%;
  margin: 0 auto;
}

@font-face {
  font-family: star;
  src: url(Starjedi.ttf);
}

html {
background: url(https://images5.alphacoders.com/725/725049.jpg) center;
background-size: auto;
}

h1 {
color : #ffc400;
text-shadow: 2px 1px rgba(0, 0, 0, 0.356);
/* font-family: star; */
}

/* p {
font-family: star;
color: #1a1a1a;
} */

.container {
display: flex;
flex-wrap: wrap;
}

.buttonContainer {
display: flex;
flex-wrap: wrap;
}

button {
background-color: #ffc400;
border-radius: 25px;
border: none;
font-family: star;
color: #1a1a1a;
}

ul {
list-style-type: none;
font-family: star;
color: #1a1a1a;
}

button:hover {
background-color: #ffc4007a;
}

.card {
margin: 0.3rem;
background-color: #ffffff1c;
border-radius: 25px;
border: 1px solid #ffc400;
}

@media screen and (min-width: 600px) {
.card {
  flex: 1 1 calc(50% - 2rem);
}
}

@media screen and (min-width: 900px) {
.card {
  flex: 1 1 calc(33% - 2rem);
}
}