body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  max-width: 80%;
  margin: 0 auto;
}

@font-face {
  font-family: star;
  src: url(/projects/React-StarWars/static/media/Starjedi.cc3019aa.ttf);
}

html {
background: url(https://images5.alphacoders.com/725/725049.jpg) center;
background-size: auto;
}

h1 {
color : #ffc400;
text-shadow: 2px 1px rgba(0, 0, 0, 0.356);
/* font-family: star; */
}

/* p {
font-family: star;
color: #1a1a1a;
} */

.container {
display: -webkit-flex;
display: flex;
-webkit-flex-wrap: wrap;
        flex-wrap: wrap;
}

.buttonContainer {
display: -webkit-flex;
display: flex;
-webkit-flex-wrap: wrap;
        flex-wrap: wrap;
}

button {
background-color: #ffc400;
border-radius: 25px;
border: none;
font-family: star;
color: #1a1a1a;
}

ul {
list-style-type: none;
font-family: star;
color: #1a1a1a;
}

button:hover {
background-color: #ffc4007a;
}

.card {
margin: 0.3rem;
background-color: #ffffff1c;
border-radius: 25px;
border: 1px solid #ffc400;
}

@media screen and (min-width: 600px) {
.card {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(50% - 2rem);
          flex-basis: calc(50% - 2rem);
}
}

@media screen and (min-width: 900px) {
.card {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(33% - 2rem);
          flex-basis: calc(33% - 2rem);
}
}
